<script>
import TopArea from './components/TopArea.vue';
import DashBoard from './components/DashBoard.vue';

import dataMenu from './assets/menu.json';  // select * from get_result_json(25, 1) as get_result_json;
const menuList = dataMenu;
var menuSelectedPath = [];
