<template>
  <div class="contents-container">
    <PlantList
                v-bind:plantList="dataPlantList" 
                      :keywordList="dataKeywordPlantList"
                v-on:plantClicked="showPlantContent"
                v-on:sendKeyword="getKeywordPlantList"
                v-on:setAllPlantList="showAllPlantList"
                >
    </PlantList>
  </div>
  <div class="contents-container" v-if="showPlantListAllYn">
    <PlantListPreview
                v-bind:plantList="dataPlantList" 
                v-on:plantClicked="showPlantContent"
                v-on:sendKeyword="getKeywordPlantList"
                >
    </PlantListPreview>
  </div>
  <div class="contents-container" v-else>
    <PlantContent 
                    v-bind:plantInfo="paramPlantInfo"
                    v-on:sendKeyword="getKeywordPlantList"
                    >
    </PlantContent>
    <PlantGuide v-bind:plantInfo="paramPlantInfo">
    </PlantGuide>
  </div>
</template>

<script>
import PlantList from './PlantList.vue';
import PlantListPreview from './PlantListPreview.vue';
import PlantContent from './PlantContent.vue';
import PlantGuide from './PlantGuide.vue';
import plantListJson from '../../assets/plantList.json'; // select get_result_json(17); plant 기준
import keywordPlant from '../../assets/keywordPlant.json'; // select get_result_json(27);   keyword 기준

const dataKeywordPlantList = keywordPlant;
const dataPlantList = {};
const paramPlantInfo = [];
let showPlantListAllYn = true;

let grwng_min_tmprtr_val = 0;
let grwng_max_tmprtr_val = 0;

export default {
  props: ['plantList'], 
  components: {
    PlantList,
    PlantListPreview,
    PlantContent,
    PlantGuide,
  },
  data() {
    return {
      dataPlantList, 
      paramPlantInfo,
      dataKeywordPlantList,
      showPlantListAllYn,
      grwng_min_tmprtr_val, 
      grwng_max_tmprtr_val,
    };
  },
  created() {
    this.showAllPlantList();
  },
  mounted() {

  },
  update() {
  },
  methods: {
    showPlantContent(plantInfo) {
      this.showPlantListAllYn = false;
      this.paramPlantInfo = plantInfo;

      this.grwng_min_tmprtr_val = plantInfo.grwng_min_tmprtr_val;
      this.grwng_max_tmprtr_val = plantInfo.grwng_max_tmprtr_val;
      
      // chartData = {
      //   labels: ['최저 온도', '최고 온도'],
      //   datasets: [{
      //         label: '온도 (°C)',
      //         // data: [this.lowestTemperature, this.highestTemperature],
      //         data: [20, 20],
      //         backgroundColor: [
      //           'rgba(255, 99, 132, 0.5)', // 최고 온도 막대의 배경색
      //           'rgba(54, 162, 235, 0.5)' // 최저 온도 막대의 배경색
      //         ],
      //         borderColor: [
      //           'rgba(255, 99, 132, 1)',
      //           'rgba(54, 162, 235, 1)'
      //         ],
      //         borderWidth: 1
      //     }]
      // };
      // chartOptions = { 
      //   scales: {
      //           y: {
      //             beginAtZero: true
      //           }
      //         },
      //   responsive: true 
      // };
    },
    showKeywordPlantList(keywordPlant) {
      // 선택한 키워드코드와 일치하는 작물 목록을 담는다.
      var paramPlantKeyword = [];
      const keywordPlantList = {};
      this.dataPlantList = {};

      for (var i = 0; i < keywordPlant.plnt_list.length; i++) {          
        paramPlantKeyword.push({
                                cdtyp_cd:keywordPlant.cdtyp_cd,
                                cd:keywordPlant.cd,
                                plnt_keyword_vw:keywordPlant.plnt_keyword_vw,
                                plnt_id:keywordPlant.plnt_list[i].plnt_id,
                                plnt_nm:keywordPlant.plnt_list[i].plnt_nm,
                            });
      }

      var dataPlantListTmp = plantListJson;
      var idx = 0;

      for(i = 0; i < paramPlantKeyword.length; i++) {
        for(var j=0; j < dataPlantListTmp.length; j++) {
          if (paramPlantKeyword[i].plnt_id == dataPlantListTmp[j].plnt_id) {
            keywordPlantList[idx] = dataPlantListTmp[j];
            idx++;
          }
        }
      }
      this.dataPlantList = keywordPlantList;
      this.showPlantListAllYn = true; 
    },

    getKeywordPlantList(keywordInfo) {
      // 선택한 키워드코드와 일치하는 작물 목록을 keywordPlantList 에 담는다.
      var keywordPlantList = [];
      this.dataPlantList = {};

      for (var i = 0; i < dataKeywordPlantList.length; i++) {
        if (dataKeywordPlantList[i].cdtyp_cd == keywordInfo.cdtyp_cd
                    && dataKeywordPlantList[i].cd == keywordInfo.cd) {
          for(var j = 0; j < dataKeywordPlantList[i].plnt_list.length; j++) {
            keywordPlantList.push(dataKeywordPlantList[i].plnt_list[j].plnt_id);
          }
        }
      }

      // 추출된 plant_id 와 일치하는 정보를 식물 목록 파일에서 찾아서 반영한다.
      var dataPlantListTmp = plantListJson;
      var idx = 0;

      for(i = 0; i < keywordPlantList.length; i++) {
        for(j = 0; j < dataPlantListTmp.length; j++) {
          if (keywordPlantList[i] == dataPlantListTmp[j].plnt_id) {
            this.dataPlantList[idx] = dataPlantListTmp[j];
            idx++;
          }
        }
      }
      this.showPlantListAllYn = true; 
    },
    showAllPlantList() {
      this.dataPlantList = plantListJson;
      this.showPlantListAllYn = true;
    },
  },
};
</script>